<template lang="pug">
.comp
    .list
        img(v-for="(item, index) in list" v-lazy="item" @click="preview(index)")
</template>

<script>
export default {
    props: {
        list: {
            type: Array,
            default () {
                return [];
            }
        }
    },
    methods: {
        preview (index) {
            this.ImagePreview({
                images: this.list,
                startPosition: index
            });
        }
    }
};
</script>

<style lang="scss" scoped>
.list {
    padding: 1rem;
    img {
        width: 100%;
        display: block;
        margin-bottom: 1rem;
    }
}
</style>



